import axios from './axios';

/**
 * 上传材质信息(deprecated)
 * @param {Object} data 模型信息对象（具体数据结构看API接口文档）
 * @param {String} groupAppId workspaceId,默认为空
 * @returns {Promise}
 */
export const uploadMaterial = (data, groupAppId = '') => {
  return axios({
    method: 'post',
    url: '/show/material/upload',
    headers: {
      groupAppId,
    },
    data,
  });
};

/**
 * 上传材质信息
 * @param {Object} data 模型信息对象（具体数据结构看API接口文档）
 * @param {String} groupAppId workspaceId,默认为空
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6110
 */
export const uploadMaterialInfo = (data) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/upload',
    headers: {
      groupAppId: data.workspaceId || '',
    },
    data,
  });
};

/**
 * 获取材质详情信息
 * @param {String} materialId 材质Id
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6026
 */
export const getMaterialInfo = (materialId, language, groupAppId) => {
  return axios({
    method: 'get',
    url: '/bff-center/showCommon/getMaterialBasicInfoAndTags',
    headers: {
      language,
      groupAppId,
    },
    params: { materialId },
  });
};

/**
 * 获取材质详情信息，用于拖拽到鞋模型渲染时使用(deprecated)
 * @param {String} relateId 材质Id
 * @returns {Promise}
 */
export const getMaterialInfoForDrag = (relateId) => {
  return axios({
    method: 'get',
    url: '/catalogue/appInteraction/getMaterialDetailInfo',
    params: {
      relateId,
    },
  });
};

/**
 * 更新材质详情信息（标签更新在其他接口）
 * @param {String} data { id: string, propName: any }
 * @see {Promise}
 */
export const updateMaterialInfo = (data) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/updateNewMaterialInfo',
    data,
  });
};

/* ============================ 材质标签相关 ======================= */
/**
 * 新增材质标签
 * @param {String} id 材质ID
 * @param {Array} tags 新添加的标签数组
 * @param {String} groupAppId workspaceId,默认为空
 * @returns {Promise}
 */
export const createNewTagForMaterial = (id, tags, groupAppId = '') => {
  return axios({
    method: 'post',
    url: '/show/tag/addMaterialTag',
    headers: {
      groupAppId,
    },
    data: {
      id,
      tags,
    },
  });
};

/**
 * 删除材质标签
 * @param {String} id 材质ID
 * @param {Array} tags 新添加的标签数组
 * @param {String} groupAppId workspaceId,默认为空
 * @returns {Promise}
 */
export const deleteTagsOfMaterial = (id, tags, groupAppId = '') => {
  return axios({
    method: 'post',
    url: '/show/tag/deleteMaterialTag',
    headers: {
      groupAppId,
    },
    data: {
      id,
      tags,
    },
  });
};

// ==============================================================

// 查询类目列表(无分页)
export const fetchMaterialTypes = async (id = '-1', businessTypes = 1) => {
  let data = null;
  switch (businessTypes) {
    case 1: {
      data = (await axios({
        method: 'get',
        url: `/anta-material/category/list?parentId=${id}`,
      })).data;
      break;
    }
    case 0: {
      data = (await axios({
        method: 'get',
        url: `/show/category/list?parentId=${id}`,
      })).data;
      break;
    }
    default:
  }
  return data;
};
